@import '../../../assets/css/var';

.input_wrap {
  background: #403858;
  border-radius: 10px;
  border: 1px solid transparent;
  &:hover {
    border: 1px solid #c1a0ff;
  }

  &_active {
    border: 1px solid #c1a0ff;
  }
  &_disabled {
    input {
      text-shadow: 0 0 0 #fff;
      opacity: 0.7;
      cursor: not-allowed;
      color: transparent !important;
    }
  }
  .with_max_input {
    background: #403858;
    border: none;
    border-radius: 10px;
    font-size: 20px;
    line-height: 24px;
    font-family: Helvetica-Bold, Helvetica;
    font-weight: bold;
    color: #ffffff;
    padding: 14px;
    width: 100%;

    &:hover,
    &:focus {
      border: none;
    }
    &::placeholder {
      color: $white;
      opacity: 0.3;
    }
    input {
      background: #403858;
      font-size: 20px;
      line-height: 24px;
      font-family: Helvetica-Bold, Helvetica;
      font-weight: bold;
      color: #ffffff;
    }
  }
  .max_btn {
    background: $max-btn-bg;
    border-radius: 15px;
    padding: 4px 14px;
    cursor: pointer;
    font-size: 12px;
    font-weight: 600;
    line-height: 17px;
    color: $white;
    margin-right: 10px;
    white-space: nowrap;
  }
}
